import { TOGGLE_SIDEBAR, TOGGLE_HEADER_NAVBAR, SET_BROWSER_SIZE, SET_SELECTED_MENU } from '../constants/types';
import menu from './menuContent';

const initialState = {
  isSmallDevice: false,
  headerMinimized: true,
  displaySideBar: true,
  sideBarMenu: {
    selected: {
      id: 5,
      label: 'iExtract',
      icon: 'server',
    },
    menu,
  },
};
export default (state = initialState, action) => {
  switch (action.type) {
    case SET_BROWSER_SIZE:
      return {
        ...state,
        isSmallDevice: action.size,
        headerMinimized: action.size ? true : state.headerMinimized,
        displaySideBar: !action.size,
      };
    case TOGGLE_HEADER_NAVBAR:
      return {
        ...state,
        headerMinimized: !state.headerMinimized,
      };
    case TOGGLE_SIDEBAR: {
      return {
        ...state,
        displaySideBar: !state.displaySideBar,
      };
    }
    case SET_SELECTED_MENU:
      if (action.item.content) {
        return {
          ...state,
          sideBarMenu: {
            selected: null,
            menu: state.sideBarMenu.menu.map(x => ({
              ...x,
              content: x.content.map(
                c =>
                  c.id === action.item.id
                    ? {
                        ...c,
                        expand: !c.expand,
                      }
                    : {
                        ...c,
                        expand: false,
                      }
              ),
            })),
          },
        };
      }
      return {
        ...state,
        sideBarMenu: {
          selected: action.item,
          menu: state.sideBarMenu.menu.map(x => ({
            ...x,
            content: x.content.map(
              c =>
                c.id === action.item.id || (action.item.parentId && action.item.parentId === c.id)
                  ? {
                      ...c,
                    }
                  : {
                      ...c,
                      expand: false,
                    }
            ),
          })),
        },
      };

    default:
      return state;
  }
};
